<template>
    <div class="active-container">
        <div class="main">
            <img src="../assets/images/img-activeOne-1.jpg" />
            <active-text :avatar="from_head_image" :nickName="from_nick_name"></active-text>
            <div class="from-group-block">
                <div class="from-group" v-if="area.length">
                    <input type="text" name="" v-model="area_name" class="form-input" readonly />
                    <img src="../assets/images/btn_selectRegion.png" class="btn-verifyCode" @click="showSheetVisible" />
                </div>
                <div class="from-group">
                    <input type="number" name="" v-model="form_data.mobile" placeholder="请输入手机号码" class="form-input" />
                </div>
                <div class="from-group last">
                    <input type="number" name="" v-model="form_data.verify_coder" placeholder="请输入验证码" class="form-input" />
                    <img src="../assets/images/btn_verifyCode.png" class="btn-verifyCode" @click="send_code" />
                </div>
            </div>
            <active-btn :btnImg="btnImg" :submit="invit"></active-btn>
        </div>
        <mt-actionsheet :actions="actions" v-model="sheetVisible">
        </mt-actionsheet>
    </div>
</template>
<style lang="less">
img {
    max-width: 100%
}

.active-container {
    position: absolute;
    width: 100%;
    min-height: 100%;
    background-size: 100% auto;
    background-color: #fed23d;
    background-repeat: no-repeat;
    text-align: center;
    .main {
        max-width: 750px;
        min-width: 320px;
        margin: 0 auto;
    }
    .from-group-block {
        margin-top: 20px;
    }
    .from-group {
        display: flex;
        width: 90%;
        margin: 0 auto 10px;
    }
    .from-group.last {
        margin-bottom: 0;
    }
    .form-input {
        border: 0;
        width: 100%;
        height: 40px;
        padding: 10px;
        display: block;
    }
    .btn-verifyCode {
        height: 40px;
        margin-left: 10px;
    }
}
</style>
<script>
import Vue from 'vue';
import api from '../config/api';
import activeText from '../components/activeText.vue'
import activeBtn from '../components/activeBtn.vue'
import { Toast, Indicator, MessageBox, Actionsheet } from 'mint-ui'
Vue.component(Actionsheet.name, Actionsheet);
export default {
    components: {
        activeText,
        activeBtn
    },
    data() {
        return {
            btnImg: require('@/assets/images/btn_success.png'),
            from_head_image: '',
            from_nick_name: '',
            is_disabled: false,
            scrollWatch: true,
            sheetVisible: false,
            actions: [],
            area: '',
            area_name: '',
            form_data: {
                id: '',
                mobile: '',
                verify_coder: ''
            }
        }
    },
    beforeRouteEnter(to, from, next) {
        next(vm => {

        });
    },
    created() {
        this.get();
    },
    mounted() {
        $(window).scrollTop(0);
        $(window).on('scroll', () => {
            if (this.scrollWatch) {
                //your code here
            }
        });
    },
    methods: {
        get() {
            // 抓取数据
            const self = this

            // 并发请求
            self.axios.all([self.getUserInfo(), self.getArea()])
                .then(self.axios.spread(function (userinfo, area) {
                    //两个请求现已完成

                    let [result_userinfo, result_area] = [userinfo.data, area.data];

                    self.area = result_area.area || [];

                    if (result_userinfo.status == 1) {
                        [self.from_head_image, self.from_nick_name] = [result_userinfo.from_head_image, result_userinfo.from_nick_name];
                    }
                    else {
                        MessageBox(result_userinfo.error);
                    }
                    

                }));
        },
        getUserInfo() {
            // 初始化分享者数据
            return this.axios.get(api.distributionInitRegister(), {
                params: {
                    "user_id": this.$route.query.user_id
                }
            });
        },
        getArea() {
            // 初始化手机号地区
            return this.axios.get(api.get_area());
        },
        invit() {
            // 确定提交
            const self = this;
            if (self.check()) {
                Indicator.open();
                console.log(self.form_data);
                self.axios.post(`${api.distributionRegister()}&user_id=${self.$route.query.user_id}&mobile=${self.form_data.mobile}&verify_coder=${self.form_data.verify_coder}&id=${self.form_data.id}`)
                    .then(res => {
                        let result = res.data;
                        Indicator.close();
                        if (result.status == 1) {
                            Toast({ message: result.error || '操作成功', iconClass: 'icon icon-success', duration: 1500 });
                            setTimeout(function () { self.$router.push({ path: '/activeTwo', query: { user_id: self.$route.query.user_id } }) }, 2000);
                        }
                        else {
                            Toast(result.error);
                        }
                    })
                    .catch(err => console.log(err))

            }
        },
        send_code(e) {
            // 发送验证码
            const self = this;
            if (self.is_disabled) {
                Toast('发送速度太快了');
                return false;
            }
            else {
                if (!self.form_data.mobile) {
                    Toast('请输入手机号'); return false;
                }
            }
            self.axios.get(api.get_verifycode(), {
                params: {
                    mobile: self.form_data.mobile
                }
            })
                .then(res => {
                    let result = res.data;
                    if (result.status == 1) {
                        Toast(result.error || '操作成功');
                    }
                    else {
                        if (result.error) {
                            Toast(result.error || '操作失败');
                        }
                    }

                })
                .catch(err => console.log(err))
        },
        code_lefttime_fuc(verify_name, code_lefttime) {
            // 验证码倒计时
            clearTimeout(code_timeer);
            $(verify_name).html("重新发送 " + code_lefttime);
            code_lefttime--;
            if (code_lefttime > 0) {
                $(verify_name).attr("disabled", "disabled");
                self.is_disabled = true;
                code_timeer = setTimeout(function () { self.code_lefttime_fuc(verify_name, code_lefttime); }, 1000);
            }
            else {
                code_lefttime = 60;
                self.is_disabled = false;
                $(verify_name).removeAttr("disabled");
                $(verify_name).html("发送验证码");
            }
        },
        check() {
            // 表单验证
            if (!this.form_data.mobile) {
                Toast('请输入手机号');
                return false;
            }
            else if (!this.form_data.verify_coder) {
                Toast('请输入验证码');
                return false;
            }
            else {
                return true;
            }
        },
        showSheetVisible() {
            this.sheetVisible = true;
        }
    },
    destroyed() {
        this.scrollWatch = false;
    },
    watch: {
        '$route'(to, from) {
            this.get();
        },
        area() {
            const self = this;

            self.form_data.area_name = self.area[0] && self.area[0].name || '';

            for(let obj of self.area){
                self.actions.push({
                    name: obj.name,
                    method() {
                        self.area_name = obj.name;
                        self.form_data.id = obj.id;
                    }
                });
            }
        }
    }
}
</script>


